body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-link {
  color: #61dafb;
}

.instanceStatus {
  color: #ffffff;
  padding: 2rem;
}

.button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.button {
  display: inline-block;
  *display: inline;
  zoom: 1;
  padding: 6px 20px;
  margin: 0;
  cursor: pointer;
  border: 1px solid #bbb;
  overflow: visible;
  font: bold 13px arial, helvetica, sans-serif;
  text-decoration: none;
  white-space: nowrap;
  color: #555;

  background-color: #ddd;
  background-image: -webkit-gradient(linear,
      left top, left bottom,
      from(rgba(255, 255, 255, 1)),
      to(rgba(255, 255, 255, 0)));
  background-image: linear-gradient(top,
      rgba(255, 255, 255, 1),
      rgba(255, 255, 255, 0));

  -webkit-transition: background-color 0.2s ease-out;
  transition: background-color 0.2s ease-out;
  background-clip: padding-box;
  /* Fix bleeding */
  border-radius: 3px;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.3), 0 2px 2px -1px rgba(0, 0, 0, 0.5),
    0 1px 0 rgba(255, 255, 255, 0.3) inset;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.9);

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.button:hover {
  background-color: #eee;
  color: #555;
}

.button:active {
  background: #e9e9e9;
  position: relative;
  top: 1px;
  text-shadow: none;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3) inset;
}

.button[disabled],
.button[disabled]:hover,
.button[disabled]:active {
  border-color: #eaeaea;
  background: #fafafa;
  cursor: default;
  position: static;
  color: #999;
  /* Usually, !important should be avoided but here it's really needed :) */
  box-shadow: none !important;
  text-shadow: none !important;
}

/* Smaller buttons styles */

.button.small {
  padding: 4px 12px;
}

/* Larger buttons styles */

.button.large {
  padding: 12px 30px;
  text-transform: uppercase;
}

.button.large:active {
  top: 2px;
}

/* Colored buttons styles */

.button.green,
.button.red,
.button.blue {
  color: #fff;
  text-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
  background-image: -webkit-gradient(linear,
      left top, left bottom,
      from(rgba(255, 255, 255, 0.3)),
      to(rgba(255, 255, 255, 0)));
  background-image: linear-gradient(top,
      rgba(255, 255, 255, 0.3),
      rgba(255, 255, 255, 0));
}

/* */

.button.green {
  background-color: #57a957;
  border-color: #57a957;
}

.button.green:hover {
  background-color: #62c462;
}

.button.green:active {
  background: #57a957;
}

/* */

.button.red {
  background-color: #ca3535;
  border-color: #c43c35;
}

.button.red:hover {
  background-color: #ee5f5b;
}

.button.red:active {
  background: #c43c35;
}

/* */

.button.blue {
  background-color: #269ce9;
  border-color: #269ce9;
}

.button.blue:hover {
  background-color: #70b9e8;
}

.button.blue:active {
  background: #269ce9;
}

/* */

.green[disabled],
.green[disabled]:hover,
.green[disabled]:active {
  border-color: #57a957;
  background: #57a957;
  color: #d2ffd2;
}

.red[disabled],
.red[disabled]:hover,
.red[disabled]:active {
  border-color: #c43c35;
  background: #c43c35;
  color: #ffd3d3;
}

.blue[disabled],
.blue[disabled]:hover,
.blue[disabled]:active {
  border-color: #269ce9;
  background: #269ce9;
  color: #93d5ff;
}

/* Group buttons */

.button-group,
.button-group li {
  display: inline-block;
  *display: inline;
  zoom: 1;
}

.button-group {
  font-size: 0;
  /* Inline block elements gap - fix */
  margin: 0;
  padding: 0;
  background: rgba(255, 255, 255, 0.32);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  padding: 7px;
  border-radius: 7px;
}

.button-group li {
  margin-right: -1px;
  /* Overlap each right button border */
}

.button-group .button {
  font-size: 13px;
  /* Set the font size, different from inherited 0 */
  border-radius: 0;
}

.button-group .button:active {
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.2) inset,
    5px 0 5px -3px rgba(0, 0, 0, 0.2) inset,
    -5px 0 5px -3px rgba(0, 0, 0, 0.2) inset;
}

.button-group li:first-child .button {
  border-radius: 3px 0 0 3px;
}

.button-group li:first-child .button:active {
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.2) inset,
    -5px 0 5px -3px rgba(0, 0, 0, 0.2) inset;
}

.button-group li:last-child .button {
  border-radius: 0 3px 3px 0;
}

.button-group li:last-child .button:active {
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.2) inset,
    5px 0 5px -3px rgba(0, 0, 0, 0.2) inset;
}
